.square {
	width: 60px;
	height: 60px;
	font-size: 20px;
	line-height: 60px;
	text-align: center;
	color: #000;
	transition: .5s ease-in-out;
}

@media (max-width: 500px) {
	.square {
		width: 40px;
		height: 40px;
	}
}
@media (max-width: 300px) {
	.square {
		width: 20px;
		height: 20px;
	}
}
